.project-image {
  flex: 1 1 calc(33.33% - 20px); /* Każdy obrazek zajmuje około 1/3 szerokości wiersza */
  max-width: 100%;
  height: 50vh;
  object-fit: cover;
  transition: transform 1000ms;
}
.projects-container {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;

  justify-content: center;
}

@media (max-width: 768px) {
  .project-image {
    flex: 1 1 50%; /* Na tabletach dwa obrazy na wiersz */
  }
}

@media (max-width: 480px) {
  .project-image {
    flex: 1 1 100%; /* Na telefonach jeden obraz na wiersz */
  }
}

.photo-container:is(:hover, :focus) figure::after {
  opacity: 1;
}

figure:is(:hover, :focus) figcaption {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.project-title {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  place-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
  color: #ffff;
  z-index: 1;
  opacity: 0;
  transition:
    opacity 600ms,
    transform 600ms;
}

.project-container::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200%;
  height: 200%;
  background: rgba(0, 0, 0, 0.5);
  transform-origin: center;
  display: flex;
  opacity: 0;
  transform: scale(2);
  transition: opacity 300ms;
}

.project-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  position: relative;
  overflow: hidden;
}
/* projects */
.projects-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
