/* Variables
/* ---------------------------------------------------------- */

:root {
  /* Colours */
  --color-primary: var(--ghost-accent-color, #3eb0ef);
  --color-base: #1f2024;
  --color-text: #3c94d9;
  --color-border: #464646;
  --color-bg: #ffffff;
  --color-success: #80b912;
  --color-error: #f05230;
  --button-color: #fed9c7;

  /* Fonts */
  --font-sans-serif: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  --font-serif: Georgia, Times, serif;
  --font-mono: Menlo, Courier, monospace;
  --font-light: 100;
  --font-normal: 400;
  --font-bold: 700;
  --font-heavy: 800;

  /* Breakpoints */
  --xlarge: 1680px;
  --large: 1280px;
  --medium: 980px;
  --small: 740px;
  --xsmall: 480px;

  /* Sizes */
  --height: 4rem;
  --margin: 2rem;
  --radius: 0.5rem;
}
